export default ".page-topbar {\n    min-height: 60px;\n    /*position: fixed;*/\n    width: 100%;\n    background-color: #0747A6;\n    top: 0px;\n    /*z-index:10000;*/\n    left: 0px;\n    box-shadow: 0 2px 4px rgba(33,33,33,.2);\n    -webkit-box-shadow: 0 2px 4px rgba(33,33,33,.2);\n    -moz-box-shadow: 0 2px 4px rgba(33,33,33,.2);\n    -o-box-shadow: 0 2px 4px rgba(33,33,33,.2);\n    -ms-box-shadow: 0 2px 4px rgba(33,33,33,.2);\n    max-height: 60px;\n}\n\n/*Transition==========================*/\n\n.page-topbar, .logo-area, .logo-area, #main-content, .logo-icon, .wraplist, .page-sidebar, .profile-info, .profile-image, .profile-details, .menu-title, .profile-image a img {\n    transition: 600ms;\n}\n\n.page-topbar .logo-area {\n   \n    background-repeat: no-repeat;\n}\n\n.page-topbar .logo-area {\n    width: 240px;\n    background-color: #0747A6;\n    display: block;\n    min-height: 60px;\n    float: left;\n    margin-left: 27px;\n}\n\n.page-topbar .logo-icon{\n    \n    background-repeat: no-repeat !important;\n}\n\n.page-topbar .logo-icon {\n    width: 100px !important;\n    background-color: #35395c;\n    display: block;\n    min-height: 60px;\n    float: left;\n}\n\n.page-topbar ul {\n    margin: 0px;\n    padding: 0px;\n}\n\n.page-topbar ul li {\n    list-style-type: none;    \n}\n\n.page-topbar .user-info li a {\n    display: inline-block;\n    font-size: 20px;\n    padding: 12px 20px;\n    color: #fff;\n    font-family: 'Comfortaa';\n    font-weight: 400;\n     position:relative;\n}\n\n.page-topbar .user-notify li a {\n    display: inline-block;\n    font-size: 16px;\n    padding: 22px 14px;\n    color: #fff;\n    font-family: 'Comfortaa';\n    font-weight: 400;\n     position:relative;\n}\n\n.page-topbar .quick-area .info-menu {\n    height: 60px;\n    margin: 0;\n    line-height: 50px;\n    padding: 0 15px;\n    display: flex;\n}\n\n.info-menu li{\n   \n}\n\n.page-topbar .badge {\n    display: inline-block;\n    min-width: 10px;\n    position: absolute;\n    padding: 3px 6px;\n    font-size: 10px;\n    font-weight: bold;\n    line-height: 1;\n    color: #fff;\n    text-align: center;\n    white-space: nowrap;\n    vertical-align: middle;\n    background-color: #F44336;\n    border-radius: 10px;\n    top: 8px;\n   right: 12px;\n}\n\nli.profile img {\n    height: 30px;\n    width: 30px;\n    top: 0px;\n    right: 10px;\n    position: relative;\n    background-color: #999999;\n}\n\n/* Ripple magic */\n\n.page-topbar button:focus {\n     outline: none;\n}\n\n.page-topbar button {\n    border: none;\n    width: 50px;\n    cursor: pointer;\n    height: 50px;\n    color: white;\n    padding: 11px 15px;\n    font-size: 21px;\n    border-radius: 100%;\n    background: transparent;\n    margin-top: 5px;\n    position: relative;\n    float: left;\n}\n\n.page-topbar button{\n     position: relative;\n     overflow: hidden;\n}\n\n.page-topbar button:after {\n     content: '';\n     position: absolute;\n     top: 50%;\n     left: 50%;\n     width: 5px;\n     height: 5px;\n     background: rgba(255, 255, 255, .5);\n     opacity: 0;\n     border-radius: 100%;\n     transform: scale(1, 1) translate(-50%);\n     transform-origin: 50% 50%;\n}\n\n@-webkit-keyframes ripple {\n     0% {\n         transform: scale(0, 0);\n         opacity: 1;\n    }\n     20% {\n         transform: scale(25, 25);\n         opacity: 1;\n    }\n     100% {\n         opacity: 0;\n         transform: scale(40, 40);\n    }\n}\n\n@keyframes ripple {\n     0% {\n         transform: scale(0, 0);\n         opacity: 1;\n    }\n     20% {\n         transform: scale(25, 25);\n         opacity: 1;\n    }\n     100% {\n         opacity: 0;\n         transform: scale(40, 40);\n    }\n}\n\n.page-topbar button:focus:not(:active)::after {\n     -webkit-animation: ripple 1s ease-out;\n             animation: ripple 1s ease-out;\n}\n\n.page-topbar li .dropdown-menu.profile {\n    max-width: 180px;\n    height: auto;\n    padding: 0px;\n}\n\n.dropdown-menu {\n    width: 360px;\n    border-radius: 0px;\n    -o-border-radius: 0px;\n    -ms-border-radius: 0px;\n    -moz-border-radius: 0px;\n    -webkit-border-radius: 0px;\n    line-height: 23px;\n    font-size: 14px;\n    padding-bottom: 0px;\n    border: 0px solid transparent;\n    box-shadow: 0 0 20px 0 rgba(33,33,33,.2);\n    -webkit-box-shadow: 0 0 20px 0 rgba(33,33,33,.2);\n    -moz-box-shadow: 0 0 20px 0 rgba(33,33,33,.2);\n    -o-box-shadow: 0 0 20px 0 rgba(33,33,33,.2);\n    -ms-box-shadow: 0 0 20px 0 rgba(33,33,33,.2);\n    margin-top: -1px;\n    padding-top: 0px;\n}\n\n.page-topbar .dropdown-menu {\n    background: #ffffff;\n    border: 1px solid #f5f5f5;\n}\n\n.page-topbar li .dropdown-menu.profile li {\n    line-height: 40px;\n    display: block;\n    height: 40px;\n    margin: 0px;\n}\n\n.page-topbar li .dropdown-menu.profile li a {\n    display: block;\n    border-bottom: 1px solid #eeeeee;\n    padding: 12px 8px;\n}\n\n.page-topbar .dropdown-menu:after, .page-topbar .dropdown-menu:before {\n    bottom: 100%;\n    left: 22px;\n    border: solid transparent;\n    content: \" \";\n    height: 0;\n    width: 0;\n    position: absolute;\n    pointer-events: none;\n}\n\n.page-topbar .dropdown-menu:before {\n    border-color: rgba(194, 225, 245, 0);\n    border-bottom-color: rgba(80, 80, 80, .1);\n    border-width: 10px;\n    margin-left: -10px;\n}\n\n.page-topbar li .dropdown-menu li a {\n    text-decoration: none;\n    color: #717171;\n}\n\n/*//////////////////////////SIDE BAR NAVE//////////////////////////*/\n\n.page-sidebar {\n    height: auto;\n    width: 260px;\n    position: fixed;\n    padding: 0px;\n    background-color: #ffffff;\n    margin-top: 60px;\n    overflow: hidden;\n    box-shadow: 0 0 10px 0 rgba(33,33,33,.2);\n    -webkit-box-shadow: 0 0 10px 0 rgba(33,33,33,.2);\n    -moz-box-shadow: 0 0 10px 0 rgba(33,33,33,.2);\n    -o-box-shadow: 0 0 10px 0 rgba(33,33,33,.2);\n    -ms-box-shadow: 0 0 10px 0 rgba(33,33,33,.2);\n    min-height: 100%;\n    height: auto;\n}\n\n.page-sidebar.fixedscroll {\n    position: fixed;\n}\n\n.page-sidebar.collapseit {\n    width: 60px;\n    margin-left: 0px !important;\n    z-index: 99;\n}\n\n.page-sidebar .profile-info {\n    background: transparent;\n    padding-top: 15px;\n    padding-bottom: 10px;\n    padding-left: 20px;\n    padding-right: 15px;\n    display: flex;\n}\n\n.page-sidebar .profile-image {\n    padding-right: 5px;\n    display: inline-block;\n}\n\n.profile-info .profile-image img {\n    height: 65px;\n    width: 65px;\n    float: right;\n    top: 5px;\n    left: 5px;\n    position: relative;    \n}\n\n.short-profile img {\n    height: 45px !important;\n    width: 45px !important; \n    left: 2px !important;\n}\n\n.short-profile .profile-details{\n    display: none;\n}\n\n.profile-info .profile-details h3 {\n    margin: 19px 0px 6px 14px;\n}\n\n.profile-info .profile-details .profile-title {\n    color: rgba(174, 178, 183, 1.0);\n    font-size: 13px;\n    margin: 0px 0px 6px 14px;\n}\n\n#main-menu-wrapper ul {\n    padding: 0px;\n    margin: 0px;\n    padding-left: 0px;\n    list-style-type: none;\n    font-family: 'Comfortaa';\n    font-weight: 400;\n    font-size: 14px;\n}\n\n#main-menu-wrapper .menusection {    \n    font-weight: 400;\n    font-size: 13px;\n    line-height: 40px;\n    padding-left: 24px;\n    margin: 10px 0 0px 0;\n    color: #9e9e9e;\n    text-transform: uppercase;\n}\n\n.profile-info .profile-details h3 a {    \n    font-weight: 400;\n    color: #757575;\n    font-size: 19px;\n    line-height: 23px;\n    text-decoration: none;\n}\n\n#main-menu-wrapper li a {\n    display: block;\n    line-height: 42px;\n    min-height: 42px;\n    height: auto;\n    color: #424242;\n    text-decoration: none;\n/*    padding: 14px 0px;*/\n    padding-left: 5px;\n    clear: both;\n    border-left: 4px solid transparent;\n    white-space: nowrap;\n/*    font-size: 18px;*/\n}\n\n#main-menu-wrapper .fa {\n    font-size: 14px;\n}\n\n#main-menu-wrapper li a i {\n    display: inline-block;\n    width: 40px;\n    height: 34px;\n    line-height: 34px;\n    text-align: center;\n    top: 0px;\n    position: relative;\n}\n\n#main-menu-wrapper li a:hover {\n    background-color: rgba(33, 33, 33, 0.1);\n    border-left: 4px solid #35395c;    \n}\n\n/*MAIN CONTENT/////////////////////////////////////////////////////////////*/\n\n#main-content {\n    margin-left: 260px;\n    display: block;\n}\n\n#main-content.sidebar_shift {\n    margin-left: 60px;\n    margin-right: 0px;\n}\n\n.main-wrapper {\n    margin-top: 60px;\n    display: inline-block;\n    width: 100%;\n    padding: 15px 15px 0 15px;\n    margin-left: 0px;\n    margin-right: 0px;\n}\n\nsection.box {\n    background-color: #ffffff;\n    margin: 15px 0;\n    min-height: 350px;\n    box-shadow: 0 1px 3px rgba(33,33,33,.2);\n    -webkit-box-shadow: 0 1px 3px rgba(33,33,33,.2);\n    -moz-box-shadow: 0 1px 3px rgba(33,33,33,.2);\n    -o-box-shadow: 0 1px 3px rgba(33,33,33,.2);\n    -ms-box-shadow: 0 1px 3px rgba(33,33,33,.2);\n}\n\n.crop {\n   border-radius: 100%;\n    line-height: 2em;\n    padding: 0em;\n    text-align: center;\n    margin: 0em;\n    width: 2em;\n    height: 2em;\n    float: left;\n    text-transform: uppercase;\n  }\n\n.about {\n    float: left;\n    padding-left: 0.4em;\n  }\n\n.online,\n  .offline,\n  .me,\n  .warning {\n    margin-right: 3px;\n    font-size: 0.8em;\n  }\n\n.online {\n    color: #86BB71;\n  }\n\n.offline {\n    color: #E38968;\n  }\n\n.me {\n    color: #94C2ED;\n  }\n\n.warning {\n    color: #00c8e7;\n  }\n\n.status {\n    color: #92959E;\n  }\n\n.name {\n    float: left;\n  }\n\n.page-topbar .badge {\n    display: inline-block;\n    min-width: 10px;\n    position: absolute;\n    padding: 3px 6px;\n    font-size: 10px;\n    font-weight: bold;\n    line-height: 1;\n    color: #fff;\n    text-align: center;\n    white-space: nowrap;\n    vertical-align: middle;\n    background-color: #F44336;\n    border-radius: 10px;\n    top: 8px;\n   right: 12px;\n}\n\n  \n/*